.footer {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  font-weight: bold;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 15%);
  background-color: #fff;
  position: sticky;
}

.copy-right {
  text-align: center;
  font-size: 10px;
}

.footer-social-media {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}

.footer-social-media__icon {
  margin: 0px 4px;
  width: 25px;
  height: 25px;
}

.footer-info {
  display: flex;
  justify-content: center;
}

.footer-info-left,
.footer-info-center,
.footer-info-right {
  font-size: x-small;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 30%;
}

.footer-info__name,
.footer-info__email,
.footer-info__number {
  margin-bottom: 10px;
}

.footer-info__name,
.footer-info__returns {
  text-align: start;
}

.footer-info__email,
.footer-info__terms {
  text-align: center;
}

.footer-info__number,
.footer-info__contact {
  text-align: end;
}

.footer-date {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.footer-seperator {
  flex-grow: 1;
  border: none;
  height: 1px;
  background-color: var(--color-green);
  width: 100%;
}

.footer-name {
  display: flex;
  justify-content: center;
}

.footer-bottom__name {
  text-transform: uppercase;
  font-size: large;
  margin: 0px 20px;
}
